.banner{
    background-image: url(https://images.unsplash.com/photo-1506846540181-347008948fff?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=593&q=80);
    background-repeat: no-repeat;
    background-position:center;
    background-size: cover;
    .banner-main{
        height: 480px;
    }
}

.nav-scroller{
    height: 2.75rem; /* 隱藏下方捲軸 */
    overflow-y: hidden; 
    border-bottom: 1px solid #707070;
}

.nav-scroller .nav {
    display: flex;
    flex-wrap: nowrap;
    padding-bottom: 1rem; /* 隱藏下方捲軸 */
    overflow-x:auto;
    white-space: nowrap; //文字不斷行
}

.review{
    background-image: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/exhibition-week6/people2.png?raw=true);
    height: 482px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
.primary-opacity{
    background-color:rgba(99,8,52,0.8) ;
}

.foot-banner{
    background-image: url(https://images.unsplash.com/photo-1536768767433-cc552f38def9?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=750&q=80);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

}

.h400{
    height: 400px;
}

